body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.login-page{
    height: 100vh;
    padding-top: 40vh;
    box-sizing: border-box;
    background-image: linear-gradient(to bottom, #77c9d4, #65c7c8, #58c4b9, #53c0a6, #57bc90);
}
.name{
    text-align: center;
}
.google-login-btn{
    text-align: center;
    box-sizing: border-box;
    padding: 40px;
    width: 100%;
}
.bottom-nav{
    width: 100%;
    position: fixed;
    bottom: 0;
}
.page{
    height: calc(100vh - 56px);
    box-sizing: border-box;
    width: 100vw;
    overflow-y: scroll;
    overflow-x: hidden;
}
